  transition-duration: 150ms;
}
.duration-300 {
  transition-duration: 300ms;
}

:root {
  --color-primary: #a259f7;
  --color-secondary: #f76aee;
}

/* Z-Index Layers */
:root {
  --z-background: -10;
  --z-content: 1;
  --z-header: 40;
  --z-modal: 50;
  --z-floating: 60;
}

/* Base Styles */
body {
  overflow-x: hidden;
}

/* Global fix for opacity issues */
* {
  opacity: 1 !important;
  visibility: visible !important;
}

section, div, h1, h2, h3, h4, h5, h6, p, span {
  opacity: 1 !important;
}

/* Override any animation that changes opacity */
[style*="opacity"] {
  opacity: 1 !important;
}

/* Disable all fade animations */
.fade-in, .fade-out, .animate-fade, .animate-opacity {
  opacity: 1 !important;
  animation: none !important;
}

/* Background fix */
.fixed-bg {
  position: fixed;
  z-index: var(--z-background);
}

/* Content layer */
.content-layer {
  position: relative;
  z-index: var(--z-content);
}

/* Modal layer */
.modal-layer {
  z-index: var(--z-modal) !important;
}

/* Floating buttons */
.floating-layer {
  z-index: var(--z-floating) !important;
}

/* Header */
.header-layer {
  z-index: var(--z-header) !important;
}

.text-gradient {
  background: linear-gradient(135deg, #a259f7, #f76aee);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.bg-darker {
  background-color: #0a0a0a;
}

.bg-dark {
  background-color: #111111;
}

.glass-dark {
  background: rgba(15, 15, 15, 0.8);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.grid-pattern {
  background-image: 
    linear-gradient(rgba(162, 89, 247, 0.1) 1px, transparent 1px),
    linear-gradient(90deg, rgba(162, 89, 247, 0.1) 1px, transparent 1px);
  background-size: 50px 50px;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #111111;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #a259f7, #f76aee);
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #8a42d4, #d455c7);
}

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
}

/* Custom animations */
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}

.float-animation {
  animation: float 3s ease-in-out infinite;
}

/* NEW ANIMATIONS - WITHOUT OPACITY CHANGES */

/* Star Animation - FIXED */
@keyframes twinkle {
  0%, 100% { filter: brightness(0.8); }
  50% { filter: brightness(1.2); }
}

@keyframes float-advanced {
  0%, 100% { transform: translateY(0px) rotate(0deg); }
  33% { transform: translateY(-20px) rotate(5deg); }
  66% { transform: translateY(10px) rotate(-5deg); }
}

@keyframes pulse-glow {
  0%, 100% { box-shadow: 0 0 20px rgba(162, 89, 247, 0.3); }
  50% { box-shadow: 0 0 40px rgba(162, 89, 247, 0.6); }
}

@keyframes slide-in {
  from { transform: translateX(-100%); }
  to { transform: translateX(0); }
}

@keyframes fade-up {
  from { transform: translateY(30px); }
  to { transform: translateY(0); }
}

@keyframes cosmic-sweep {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

@keyframes mystic-pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

@keyframes spin-wheel {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes shimmer {
  0% { background-position: -1000px 0; }
  100% { background-position: 1000px 0; }
}

@keyframes wave {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

@keyframes heartbeat {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

/* New Animation Classes - WITHOUT OPACITY */
.animate-twinkle {
  animation: twinkle 3s ease-in-out infinite;
}

.animate-float {
  animation: float 6s ease-in-out infinite;
}

.animate-float-advanced {
  animation: float-advanced 8s ease-in-out infinite;
}

.animate-pulse-glow {
  animation: pulse-glow 2s ease-in-out infinite;
}

.animate-slide-in {
  animation: slide-in 0.6s ease-out forwards;
}

.animate-fade-up {
  animation: fade-up 0.8s ease-out forwards;
}

.animate-shimmer {
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.1), transparent);
  background-size: 1000px 100%;
  animation: shimmer 3s infinite;
}

.animate-wave {
  animation: wave 2s ease-in-out infinite;
}

.animate-heartbeat {
  animation: heartbeat 1.5s ease-in-out infinite;
}

/* Zodiac Symbols Animation */
.zodiac-float {
  animation: float-advanced 8s ease-in-out infinite;
}

/* Cosmic Background Effects */
.cosmic-ray {
  background: linear-gradient(90deg, 
    transparent, 
    rgba(162, 89, 247, 0.2), 
    transparent
  );
  animation: cosmic-sweep 3s linear infinite;
}

/* Tarot Card Flip */
.tarot-flip {
  transform-style: preserve-3d;
  transition: transform 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.tarot-flip:hover {
  transform: rotateY(180deg);
}

.tarot-front, .tarot-back {
  backface-visibility: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
}

.tarot-back {
  transform: rotateY(180deg);
}

/* Horoscope Wheel Spinner */
.spin-wheel {
  animation: spin-wheel 20s linear infinite;
}

/* Mystic Orb */
.mystic-orb {
  background: radial-gradient(
    circle at 30% 30%,
    rgba(162, 89, 247, 0.8),
    rgba(247, 106, 238, 0.4),
    transparent 70%
  );
  animation: mystic-pulse 4s ease-in-out infinite;
}

/* Custom cursor styles */
button, a, [role="button"] {
  cursor: pointer !important;
}

/* Responsive improvements */
@media (max-width: 768px) {
  .grid-pattern {
    background-size: 30px 30px;
  }
  
  /* Fix mobile overflow */
  body {
    max-width: 100vw;
    overflow-x: hidden;
  }
  
  /* Modal fixes for mobile */
  .modal-fix {
    padding: 1rem !important;
  }
  
  /* Form fixes for mobile */
  input, select, textarea {
    font-size: 16px !important; /* Prevents zoom on iOS */
  }
}

/* Form fixes */
input:focus, select:focus, textarea:focus {
  outline: none !important;
  box-shadow: 0 0 0 2px rgba(162, 89, 247, 0.5) !important;
}

/* Modal backdrop fix */
.modal-backdrop {
  background: rgba(0, 0, 0, 0.8) !important;
  backdrop-filter: blur(10px) !important;
}

/* Ensure content stays above background */
.section-wrapper {
  position: relative;
  z-index: 1;
  background: transparent;
}

/* Card hover effects */
.hover-lift {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.hover-lift:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(162, 89, 247, 0.2);
}

/* Fix for form overflow */
.form-container {
  max-height: 90vh;
  overflow-y: auto;
}

/* Custom focus styles */
*:focus {
  outline: 2px solid rgba(162, 89, 247, 0.5);
  outline-offset: 2px;
}

/* Selection styles */
::-moz-selection {
  background: rgba(162, 89, 247, 0.3);
  color: white;
}
::selection {
  background: rgba(162, 89, 247, 0.3);
  color: white;
}

::-moz-selection {
  background: rgba(162, 89, 247, 0.3);
  color: white;
}

/* Smooth transitions for all interactive elements */
button, a, .transition-all {
  transition-property: all;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
}

/* Backdrop blur support fallback */
@supports not (backdrop-filter: blur(10px)) {
  .glass-dark {
    background: rgba(15, 15, 15, 0.95);
  }
}

/* Print styles */
@media print {
  .glass-dark,
  .bg-darker,
  .bg-dark {
    background: white !important;
    color: black !important;
  }
  
  .text-gradient {
    background: none !important;
    -webkit-text-fill-color: black !important;
    color: black !important;
  }
}

/* FIXES ADDED BELOW */

/* Force all sections to be fully visible */
section {
  opacity: 1 !important;
  visibility: visible !important;
}

/* Remove any fade effects from motion components */
.motion-div-fix {
  opacity: 1 !important;
}

/* Ensure all text is visible with good contrast */
.text-visibility-fix {
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.5);
}

/* Improve contrast for light text */
.text-gray-300 {
  color: rgba(209, 213, 219, 1) !important;
}

.text-gray-400 {
  color: rgba(156, 163, 175, 1) !important;
}

/* Fix booking form overflow */
.booking-form-fix {
  max-height: 85vh;
  overflow-y: auto;
}

@media (max-width: 768px) {
  .booking-form-fix {
    max-height: 90vh;
    margin: 1rem;
  }
}

/* Fix form zoom issue on iOS */
input, select, textarea {
  font-size: 16px !important;
}

/* Improve scroll performance */
html {
  scroll-behavior: smooth;
}

/* Prevent horizontal scroll */
body {
  overflow-x: hidden;
  width: 100%;
}

/* Remove opacity from all animations */
@keyframes no-opacity {
  0% { transform: translateY(0); }
  100% { transform: translateY(0); }
}
